.backgroundStyle {
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  width: 100%;
  height: 100%;
}
.pageContent {
  position: absolute;
  top: 0;
  width: 99.5vw;
  margin: 0 auto;
}
.switchBackgroundIconContainer {
  display: flex;
  position: absolute;
  bottom: 5%;
  right: 5%;
  z-index: 6;

  .iconContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.638);
    color: rgba(250, 250, 250, 0.702);
    padding: 10% 40%;
    margin-right: 4px;
    border-radius: 9%;
    cursor: pointer;
    font-size: 40px;

    &:hover {
      background-color: rgba(0, 0, 0, 0.802);
      color: rgba(250, 250, 250, 0.9);
    }
  }
}
